import React from "react"
import List from "./List"
// React-Component это основной объект Приложения
class App extends React.Component {
    // Функция которая настраивает React-Component
    constructor(props){
        // Передаём основную настройку в библиотеку React
        super(props)
        // Создаём парметры, которые должны хранится в памяти !приложения!
        this.state = {
            items: localStorage.getItem("items") != null ? JSON.parse(localStorage.getItem("items")) : [{"text": "Добавь дело!"}]
        }
        // Подключаем к Event-Функции функцию Компонента 
        this.addTodo = this.addTodo.bind(this)
        this.deleteTodo = this.deleteTodo.bind(this)
    }

    // Event-Функция во время события Submit для добавления нового дела
    addTodo(e){
        // Отменяем перезагрузку страницы
        e.preventDefault()
        // Элемент который вызвал событие
        let form = e.target
        let newTodo = form.todoInput.value
        // Изменить state
        // .setState( (state)=>{............      return {state}} )
        // Обновить state и добавить в items новое дело
        
        this.setState( (state)=>{
            // Берём мозг
            let newState = state
            // Обновляем память items
            // Если текст не пустой, то добавляем
            if(newTodo !== ""){
                newState.items.push( {text: newTodo} )
            }
            localStorage.setItem("items", JSON.stringify(newState.items)) 
            // Возвращаем мозг
            return {newState}
        })

        // Очистить input
        form.todoInput.value = ""
    }
    // Event-Функция во время события Click для удаления дела
    deleteTodo(e){
        // Выходим к родителю, берём первый элемент (это наш span)
        let todoIndex = e.target.parentNode.childNodes[0].innerHTML -1
        // Обновить состояние state, удалить дело по индексу
        // ****************
        this.setState( (state)=>{
            let newState = state
            let items = newState.items
            items.splice( todoIndex, 1)
            newState.items = items;
            return {newState}

        })

        alert(todoIndex)
    } 


    // Рендер нашего компонента
    render(){
        
        let elem = 
        <div id="todo-list" className="container-md">
            <div className="row justify-content-center">
                <div className="col-4">
                    <form className="row" onSubmit={this.addTodo}>
                        <input name="todoInput" type="text" className="col form-control" placeholder="Введите дело"/>
                        <button type="submit" className="col-3 btn btn-danger">Добавить</button>
                    </form>
                </div>
            </div>
            <div className="row justify-content-center">
                <div className="col-4">
                    <List itemList={this.state.items} deleteTodo={this.deleteTodo}/>
                </div>
            </div>
        </div>

        return elem
    }
}

export default App