function List(props){
    let items = props.itemList.map((item, index)=>{
        // 1. Купить молоко (Иконка)
        return <div className="row justify-content-around">
            <span className="col-1">{index+1}</span>.
            <div className="col-5">{item.text}</div> 
            <i className="bi bi-trash-fill col-1" onClick={props.deleteTodo} ></i>
        </div>
    })

    let elem = 
    <div>
        {items}
    </div>


    return elem
}

export default List